-gold-from, @focus-gold-to);\n  color: @focus-text-color;\n}\n\n.focus-cashier-item-color(@deg: 80deg) { // 收银台Focus金色背景与文字\n  .ps-gradient(@deg, @focus-cashier-gold-from, @focus-cashier-gold-to);\n  color: @focus-text-color;\n}\n\n.bg-normal() {\n  .radial-gradient(circle, @bg-normal-from, @bg-normal-to);\n}\n\n.scale(@radix) {\n  transform: scale(@radix);\n}\n\n.ellipsis() {\n  overflow: hidden;\n  white-space: nowrap;\n  text-overflow: ellipsis;\n}\n\n.line-clamp(@val) {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: @val;\n  /*! autoprefixer: off */\n  -webkit-box-orient: vertical;\n  /* autoprefixer: on */\n}\n\n.size(@v) {\n  width: @v;\n  height: @v;\n}\n\n.size(@w, @h) {\n  width: @w;\n  height: @h;\n}\n\n.palt(@left, @top) {\n  position: absolute;\n  left: @left;\n  top: @top;\n}\n\n.cutout-circle(@height, @color) {\n  width: unit(@height / 2, rem);\n  height: unit(@height, rem);\n  position: relative;\n  overflow: hidden;\n  &:before {\n    content: '';\n    width: unit(@height, rem);\n    height: unit(@height, rem);\n    border-radius: unit(@height, rem);\n    border: unit(@height / 2, rem) solid @color;\n    position: absolute;\n    top: unit(-(@height / 2), rem);\n    left: unit(-@height, rem);\n  }\n}\n\n.show {\n  opacity: 1;\n}\n\n.hide {\n  opacity: 0;\n}\n// 上下左右垂直居中\n.center-pa-size(@w, @h) {\n  position: absolute;\n  left: 0;\n  right: 0;\n  top: 0;\n  bottom: 0;\n  margin: auto;\n  width: @w;\n  height: @h;\n}\n\n// 图片的宽度自适应父容器\n.img-responsive() {\n  vertical-align: top;\n  width: 100%;\n  height: auto;\n}\n// 可访问性隐藏\n// 一般用于图标的解释文字\n.clip() {\n  position: absolute;\n  clip: rect(0 0 0 0);\n}\n",".show {\n  opacity: 1;\n}\n.hide {\n  opacity: 0;\n}\n/**\n * iconfont项目地址\n * http://www.iconfont.cn/manage/index?manage_type=myprojects&projectId=530769\n *\n */\n@font-face {\n  font-family: \"iconfont\";\n  src: url('../../assets/fonts/iconfont.eot');\n  src: url('../../assets/fonts/iconfont.ttf') f